* {
  font-family: Helvetica, Arial,  sans-serif;
}
body {
  background-color: rgb(251, 255, 251);
  padding: 50px;
}
input {
  font-size: 1.2rem;
  color: rgb(83, 38, 38);
  width: 90%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}



.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 160px;
}